<template>
  <div class="content invite">
    <div id="" class="content-title content-pad">
          {{ $t('inviteCode.title') }}
    </div>
    <p class="invite-code content-pad">{{ $t('inviteCode.myInviteCode') }} <span id="invite-code">{{ inviteDetail.invite_code }}</span></p>
    <div class="invite-detail content-pad" style="margin-bottom: 44px;">
        <div class="clearfix address">
        <div class="pull-left invite-owner">
          <p class="des">{{ $t('inviteCode.myInviteLink') }}</p>
          <div class="clearfix">
            <div id="invite-url" class="pull-left">{{ inviteDetail.invite_url }}</div>
            <el-button type="primary" class="pull-left" v-clipboard:copy="inviteDetail.invite_url" v-clipboard:success="handleCopySuccess" v-clipboard:error="handleCopyError">{{ $t('copy') }}</el-button>
          </div>
        </div>
        <div class="pull-right">
          <div class="code" id="invite-url-qrcode"><img :src="inviteDetail.invite_url_qrcode"></div>
          <p>{{ $t('inviteCode.inviteFriend') }}</p>
        </div>
      </div>
    </div>
    <div class="invite-rules content-pad hidden">
      <div>{{ $t('inviteCode.rule.title') }}</div>
      <p class="gray">{{ $t('inviteCode.rule.part1') }}</p>
      <p class="gray">{{ $t('inviteCode.rule.part2') }}</p>
      <p class="gray">{{ $t('inviteCode.rule.part3') }}</p>
    </div>
    <div class="split"></div>
    <div class="invite-count row content-pad">
      <div class="col-md-6">
        <div class="title">{{ $t('inviteCode.inviteNum') }}</div>
        <p id="invite-num">{{ inviteDetail.invite_num }}</p>
      </div>
      <div class="col-md-6">
        <div class="title">{{ $t('inviteCode.reword') }}</div>
        <p id="reward-num">{{ inviteDetail.reward_num }}</p>
      </div>
    </div>
    <div class="split"></div>
    <div class="invite-table content-pad">
      <div class="title">{{ $t('inviteCode.record') }}</div>
        <el-table
          :data="inviteList"
          style="width: 100%">
          <el-table-column
            :label="$t('inviteCode.time')">
            <template slot-scope="scope">
              <div class="time">{{ scope.row.create_time }}</div>
            </template>
          </el-table-column>
          <el-table-column
            prop="invite_user_account"
            :label="$t('inviteCode.account')">
          </el-table-column>
          <el-table-column
            :label="$t('inviteCode.status')">
            <template slot-scope="scope">
              <div v-if="scope.row.status == 1">{{ $t('inviteCode.success') }}</div>
              <div v-else>{{ $t('inviteCode.error') }}</div>
            </template>
          </el-table-column>
        </el-table>
    </div>
    
  </div>
</template>
<script>
  import { getUserInviteInfo, getUserInviteList } from '@/api/user'
  export default {
    data() {
      return {
        inviteDetail: {},
        inviteList: null
      }
    },
    mounted() {
      this.getUserInviteInfoDetail()
      this.getUserInviteListDetail()
    },
    methods: {
      getUserInviteInfoDetail() {
        getUserInviteInfo().then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.inviteDetail = res.data
        }).catch(() => {
  
        })
      },
      getUserInviteListDetail() {
        getUserInviteList().then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          if (res.data !== '') {
            this.inviteList = res.data
          }
        }).catch(() => {
  
        })
      },
      handleCopySuccess() {
        this.$message.success(this.$t('errorTips.copySuccess'))
      },
      handleCopyError() {
        this.$message.error(this.$t('errorTips.copyError'))
      },
      copyUrl() {

      },
      add0(m) {
        return m < 10 ? '0' + m : m
      },
      format(shijianchuo) {
        // shijianchuo是整数，否则要parseInt转换
        var time = new Date(shijianchuo)
        var y = time.getFullYear()
        var m = time.getMonth() + 1
        var d = time.getDate()
        var h = time.getHours()
        var mm = time.getMinutes()
        var s = time.getSeconds()
        return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
      }
    }
  }
</script>
<style lang="less" scoped>
  .content{
    background: white;
    padding: 20px 0px;
    font-size: 14px;
    color: #3e4558;
  }
  .invite .content-pad {
    padding-left: 25px;
    padding-right: 25px;
  }

  .content-title {
    padding-top: 9px;
    padding-bottom: 18px;
    border-bottom: 1px solid #e6eaf0;
    font-size: 16px;
  }
  .invite-detail .address {
    border: 1px solid #e6eaf0;
    width: 100%;
    .invite-owner{
      padding: 50px 0 36px 13px;
      font-size: 14px;
    }
  }
  .invite-detail .address p.des{
    margin-bottom: 20px;
  }
  .invite-detail  .address .pull-right .code img{
    width: 96px;
    height: 96px;
    margin-bottom: 6px;
  }
  .invite-detail  .address .pull-right{
    text-align: center;
    margin: 10px 20px 0 0;
    font-size: 12px;
  }

  .invite .invite-code{
    margin: 30px 0 20px;
    line-height: 27px;
    color: #666666;
  }

  .invite .invite-code span{
    color: #3e4558;
    font-size: 18px;
    margin-left: 15px;
  }

  .gray{
    color: #9397a0;
  }
  .invite-rules{
    margin: 44px 0 27px 0;
    font-size: 12px;
    div{
      margin: 10px 0;
    }
    p{
      padding-bottom: 10px;
    }
  }
  .split{
    background: #f7f7fa;
    height: 20px;
  }
  .nav-second .invite{
    padding-left: 0;
    padding-right: 0;
  }
  .invite  .content-pad{
    padding-left: 25px;
    padding-right: 25px;
  }
  .invite-count{
    padding-top: 30px;
    padding-bottom: 40px;
  }
  .invite-count .title{
    font-size: 16px;
    margin: 10px 0;
  }
  .invite-count p{
    font-size: 18px;
  }
  .invite-table{
    margin-bottom: 70px;
  }
  .invite-table .title{
    line-height: 40px;
    font-size: 16px;
  }

  .avatar-btns button {
      height: 35px;
      margin-left: 5px !important;
  }
  .row{
    display: flex;
    .col-md-6{
      flex: 1;
      vertical-align: middle;
      img{
        width: 21px;
        height: 21px;
        display: inline-block;
        float: left;
      }
      span{
        display: inline-block;
      }
    }
  }
  #invite-url{
    display: inline-block;
    color: #3e4558;
    font-size: 18px;
    width: 400px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
</style>

<style lang="less">
  .invite{
    .el-button--primary{
      display: inline-block;
      padding: 3px 12px;
      margin-right: 18px;
      border: 1px solid #1fa8f6;
      border-radius: 5px;
      font-size: 12px;
      margin-top: 4px;
    }
  }

</style>



